// Angular
import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {CommonModule} from '@angular/common';
// Core Module
import {CoreModule} from '../../../core/core.module';
import {PartialsModule} from '../../partials/partials.module';
import {DashboardComponent} from './dashboard.component';
import {ProfileComponent} from './profile/profile/profile.component';
import {FormsModule, ReactiveFormsModule} from "@angular/forms";
// Material
import {
    MatAutocompleteModule,
    MatButtonModule,
    MatCardModule,
    MatCheckboxModule,
    MatDatepickerModule,
    MatDialogModule,
    MatExpansionModule,
    MatIconModule,
    MatInputModule,
    MatMenuModule,
    MatNativeDateModule,
    MatPaginatorModule,
    MatProgressBarModule,
    MatProgressSpinnerModule,
    MatRadioModule,
    MatSelectModule,
    MatSnackBarModule,
    MatSortModule,
    MatTableModule,
    MatTabsModule,
    MatTooltipModule
} from '@angular/material';
import {FlexModule} from "@angular/flex-layout";
import {NgxPermissionsGuard} from "ngx-permissions";


const routes: Routes = [{
    path: '',
    component: DashboardComponent,
    canActivate: [NgxPermissionsGuard],
    data: {
        permissions: {
            only: ['ALL_HOME'],
            redirectTo: '/error/403',
        },
        title: 'DashBoard'
    }
},
    {
        path: 'profile',
        component: ProfileComponent,
        canActivate: [NgxPermissionsGuard],
        data: {
            title: 'Profile'
        }
    },];

@NgModule({
    imports: [
        MatButtonModule,
        MatMenuModule,
        MatSelectModule,
        MatInputModule,
        MatTableModule,
        MatAutocompleteModule,
        MatRadioModule,
        MatIconModule,
        MatNativeDateModule,
        MatProgressBarModule,
        MatDatepickerModule,
        MatCardModule,
        MatPaginatorModule,
        MatSortModule,
        MatCheckboxModule,
        MatProgressSpinnerModule,
        MatSnackBarModule,
        MatExpansionModule,
        MatTabsModule,
        MatTooltipModule,
        MatDialogModule,
        CommonModule,
        PartialsModule,
        CoreModule,
        FormsModule,
        ReactiveFormsModule,
        FlexModule,
        RouterModule.forChild(routes),
    ],
    providers: [],
    declarations: [
        DashboardComponent,
        ProfileComponent,
    ]
})
export class DashboardModule {
}
